.carousel-container {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 40px 20px;
  background-color: #f5f5f5;
}

.carousel-title {
  font-size: 28px;
  margin-bottom: 10px;
}

.carousel-desc {
  color: #555;
  max-width: 600px;
  margin: 0 auto 30px;
}

.imac-frame {
  position: relative;
  width: 700px;
  max-width: 90%;
  margin: 0 auto;
  padding: 40px 20px 60px;
  background: #d0d0d0;
  border-radius: 20px;
  box-shadow: inset 0 0 0 20px #fff, 0 8px 20px rgba(0, 0, 0, 0.2);
}

.video-player {
  width: 100%;
  max-height: 400px;
  border: 6px solid #333;
  border-radius: 8px;
  background: black;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 28px;
  background: rgba(255, 255, 255, 0.6);
  border: none;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 50%;
  user-select: none;
}

.arrow.left {
  left: -30px;
}

.arrow.right {
  right: -30px;
}

.arrow:hover {
  background: rgba(255, 255, 255, 0.9);
}

.our-services-title {
  margin-top: 150px;
padding: 20px;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: 300;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}
.our-services-title::after {
  content: '';
  display: block;
  width: 10rem;
  height: 3px;
  background-color: #ffa500;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  /* margin-top: 10px;  */
}
