.subservices-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  border-radius: 8px;
  width: 90%;
  margin-left: 10px;
  margin-right: 10px;
  max-width: screen;
  max-height: screen;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #eee;
}

.modal-header h2 {
  margin: 0;
  color: #333;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
}

.modal-body {
  display: flex;
  padding: 20px;
}

.subservice-form {
  flex: 1;
  padding-right: 20px;
  border-right: 1px solid #eee;
}

.subservices-list {
  flex: 1;
  padding-left: 20px;
  max-height: 70vh;
  overflow-y: auto;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.form-group textarea {
  min-height: 80px;
  resize: vertical;
}

.prices-section {
  margin-bottom: 15px;
}

.price-row {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  align-items: center;
}

.price-row input {
  flex: 2;
}

.price-row select {
  flex: 1;
}

.remove-price {
  background: #ff6b6b;
  color: white;
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.add-price {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 5px;
}

.file-upload {
  margin-bottom: 15px;
}

.file-upload input {
  width: 100%;
}

.form-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.form-actions button {
  padding: 8px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-actions button:first-child {
  background-color: #4CAF50;
  color: white;
}

.form-actions button:last-child {
  background-color: #f44336;
  color: white;
}

.subservices-list h3 {
  margin-top: 0;
  color: #333;
}

.subservices-list ul {
  list-style: none;
  padding: 0;
}

.subservices-list li {
  padding: 15px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subservice-info h4 {
  margin: 0 0 5px 0;
  color: #333;
}

.subservice-info p {
  margin: 0 0 10px 0;
  color: #666;
}

.price-tags {
  display: flex;
  gap: 5px;
}

.price-tags span {
  background: #e3f2fd;
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.subservice-actions {
  display: flex;
  gap: 10px;
}

.subservice-actions button {
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.subservice-actions button:first-child {
  background-color: #2196F3;
  color: white;
}

.subservice-actions button:last-child {
  background-color: #f44336;
  color: white;
}

@media (max-width: 768px) {
  .modal-body {
    flex-direction: column;
  }
  
  .subservice-form {
    padding-right: 0;
    border-right: none;
    border-bottom: 1px solid #eee;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  
  .subservices-list {
    padding-left: 0;
  }
}

