.career-yellow-section-title {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    margin: 2.5rem 0 1rem 0;
    color: #222;
}

.career-yellow-openings {
    display: flex;
    gap: 2rem;
    justify-content: center;
    margin: 2rem 0;
    flex-wrap: wrap;
}

.career-yellow-card {
    background: #fff8e1;
    border: 2px solid #FFBA00;
    border-radius: 16px;
    padding: 2rem 1.5rem;
    width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    transition: box-shadow 0.3s ease;
}

.career-yellow-image {
    width: 70px;
    height: 70px;
    background: #FFBA00;
    border-radius: 50%;
    margin-bottom: 1.2rem;
    object-fit: cover;
}

.career-yellow-title {
    font-size: 1.2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    color: #333;
    text-align: center;
}

.career-yellow-desc {
    font-size: 1rem;
    color: #555;
    text-align: center;
    margin-bottom: 1.2rem;
}

.career-yellow-apply {
    background: #FFBA00;
    color: #222;
    border: none;
    border-radius: 8px;
    padding: 0.6rem 2rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s ease-in-out;
}

.career-yellow-apply:hover {
    background: #f9a825;
    color: #fff;
}

/* Animation: fade-in */
@keyframes fadein {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.animate-fadein {
    animation: fadein 0.9s ease-out;
}

/* Animation: hover scale */
.animate-hover-scale {
    transition: transform 0.25s ease, box-shadow 0.25s;
}

.animate-hover-scale:hover {
    transform: scale(1.07);
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
}

.yellow-banner-section {
    position: relative;
    background: linear-gradient(to bottom, #fff8e1, #FFBA00);
    color: #222;
    padding: 4rem 2rem 3rem 2rem;
    overflow: hidden;
    z-index: 1;
}

.yellow-banner-bg-svg::before {
    content: "";
    position: absolute;
    top: -50px;
    left: -100px;
    width: 200%;
    height: 200%;
    background: url("data:image/svg+xml,%3Csvg viewBox='0 0 1440 320' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23ffeb3b' fill-opacity='0.4' d='M0,192L80,202.7C160,213,320,235,480,229.3C640,224,800,192,960,160C1120,128,1280,96,1360,80L1440,64V0H1360C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0H0Z'/%3E%3C/svg%3E") no-repeat center;
    background-size: cover;
    z-index: 0;
    opacity: 0.6;
    pointer-events: none;
}

.yellow-banner-content {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 2.5rem;
    max-width: 1000px;
    margin: 0 auto;
    z-index: 2;
}

.yellow-banner-logo {
    width: 90px;
    height: 90px;
    background: #FFBA00;
    border-radius: 18px;
    flex-shrink: 0;
    margin-right: 2rem;
    object-fit: cover;
}

.yellow-banner-title {
    font-size: 2.2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.yellow-banner-title-main {
    color: #222;
}

.yellow-banner-title-highlight {
    color: #FFBA00;
    margin-left: 0.4rem;
}

.yellow-banner-subtitle {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 1rem 0 0.5rem 0;
    color: #333;
}

.yellow-banner-description {
    font-size: 1.05rem;
    color: #444;
    line-height: 1.6;
}

.yellow-banner-label {
    position: absolute;
    right: 3rem;
    top: 2.5rem;
    font-size: 2rem;
    color: #999;
    font-weight: 700;
    opacity: 0.25;
    z-index: 1;
}

.our-cc {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5vw;
}

.under {
    margin-top: 10vh;
    font-size: 2.5rem;
    margin-bottom: 1rem;
    text-align: center;
    font-weight: 400;
    padding-bottom: 10px;
}

.under::after {
    content: '';
    display: block;
    width: 10rem;
    height: 3px;
    background-color: #ffa500;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.service-title {
    font-size: 2rem;
    color: #2c3e50;
    margin-bottom: 1rem;
    text-align: center;
    position: relative;
    text-transform: capitalize;
}

/* Responsive Styles */
@media (max-width: 1024px) {
    .yellow-banner-content {
        gap: 2rem;
        padding: 0 1.5rem;
    }
    
    .yellow-banner-logo {
        width: 80px;
        height: 80px;
        margin-right: 1.5rem;
    }
    
    .yellow-banner-title {
        font-size: 2rem;
    }
    
    .yellow-banner-subtitle {
        font-size: 1.3rem;
    }
    
    .yellow-banner-description {
        font-size: 1rem;
    }
}

@media (max-width: 768px) {
    .yellow-banner-section {
        padding: 3rem 1.5rem;
    }
    
    .yellow-banner-content {
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 1.5rem;
    }
    
    .yellow-banner-logo {
        margin-right: 0;
        margin-bottom: 1rem;
    }
    
    .yellow-banner-title {
        font-size: 1.8rem;
    }
    
    .yellow-banner-subtitle {
        font-size: 1.2rem;
        margin: 0.8rem 0 0.4rem 0;
    }
    
    .yellow-banner-description {
        font-size: 0.95rem;
    }
    
    .yellow-banner-bg-svg::before {
        top: -30px;
        left: -50px;
    }
    
    .career-yellow-openings {
        gap: 1.5rem;
    }
    
    .career-yellow-card {
        width: 100%;
        max-width: 350px;
    }
    
    .under {
        font-size: 2rem;
        margin-top: 8vh;
    }
    
    .under::after {
        width: 8rem;
    }
}

@media (max-width: 480px) {
    .yellow-banner-section {
        padding: 2.5rem 1rem;
    }
    
    .yellow-banner-title {
        font-size: 1.6rem;
    }
    
    .yellow-banner-subtitle {
        font-size: 1.1rem;
    }
    
    .yellow-banner-logo {
        width: 70px;
        height: 70px;
    }
    
    .yellow-banner-description br {
        display: none;
    }
    
    .career-yellow-section-title {
        font-size: 1.7rem;
    }
    
    .career-yellow-card {
        padding: 1.5rem 1rem;
    }
    
    .career-yellow-title {
        font-size: 1.1rem;
    }
    
    .career-yellow-desc {
        font-size: 0.95rem;
    }
    
    .under {
        font-size: 1.7rem;
        margin-top: 6vh;
    }
    
    .under::after {
        width: 6rem;
        height: 2px;
    }
    
    .service-title {
        font-size: 1.7rem;
    }
}