.service-card {
  padding: 0px;
  margin: 0px;
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
  font-family: 'Arial', sans-serif;
  color: #333;
  line-height: 1.6;
}

.our-cc {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5vw;
}

.our-cc h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 300;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.our-cc h1::after {
  content: '';
  display: block;
  width: 10rem;
  height: 3px;
  background-color: #ffa500;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.service-title {
  font-size: 2rem;
  color: #2c3e50;
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  text-transform: capitalize;
}

.service-subtitle {
  font-size: 1.2rem;
  color: #7f8c8d;
  margin-bottom: 2rem;
  text-align: center;
}

.feature-section {
  margin-bottom: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.feature-title {
  font-size: 1.5rem;
  color: #2c3e50;
  margin-bottom: 1rem;
}

.feature-title.highlighted {
  color: #e74c3c;
}

.feature-text {
  font-size: 1rem;
  margin-bottom: 1.5rem;
  color: #34495e;
}

.icon-placeholder {
  width: 14%;
  height: 13rem;
  background-color: #E6A800;
  margin: 1.5rem;
  border-radius: 100px;
  color: #7f8c8d;
  font-weight: bold;
}

.services-right {
  width: 80%;
  margin-left: 2px;
  margin-right: 2px;
  background-color: #ffbb005b;
  padding: 1rem;
  padding-left: 3rem;
  padding-bottom: 1.5rem;
  border-top-left-radius: 100px;
  border-bottom-left-radius: 100px;
}

.services-left {
  width: 80%;
  margin-left: 2px;
  margin-right: 2px;
  background-color: #ffbb005b;
  padding: 1rem;
  padding-left: 1.7em;
  padding-bottom: 1.5rem;
  border-top-right-radius: 100px;
  border-bottom-right-radius: 100px;
}

/* Previous CSS remains the same until the media queries */

@media (max-width: 576px) {
  .our-cc h1 {
    font-size: 1.8rem;
  }
  
  .service-subtitle {
    font-size: 0.9rem;
  }
  
  .feature-section {
    flex-direction: column;
  }
  
  .services-right,
  .services-left {
    width: 90%;
    border-radius: 20px;
    margin: 0.5rem 0;
    padding: 1rem;
    /* Remove any specific left/right padding */
    padding-left: 1rem;
    padding-right: 1rem;
    /* Standardize the border radius */
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  
  .icon-placeholder {
    width: 30%;
    height: 6rem;
    margin: 0 0 1rem 0; /* Only bottom margin */
    order: -1; /* This will place ALL icons above their text */
  }
  
  /* Remove the alternating order for mobile */
  .feature-section:nth-child(odd) .icon-placeholder {
    order: -1; /* Same as all others */
  }
}

@media (max-width: 400px) {
  .our-cc h1 {
    font-size: 1.5rem;
  }
  
  .service-subtitle {
    font-size: 0.8rem;
  }
  
  .services-right h2,
  .services-left h2 {
    font-size: 1.1rem;
  }
  
  .services-right p,
  .services-left p {
    font-size: 0.8rem;
  }
  
  .icon-placeholder {
    width: 40%;
    height: 5rem;
  }
}