.career-page {
  background: #fff;
  min-height: 100vh;
}

.career-banner {
  background: #444;
  color: #fff;
  padding: 3.5rem 0 2.5rem 0;
  position: relative;
}

.career-banner-content {
  display: flex;
  align-items: flex-start;
  gap: 2.5rem;
  max-width: 900px;
  margin: 0 auto;
}

.career-banner-logo {
  width: 90px;
  height: 90px;
  background: #bbb;
  border-radius: 18px;
  margin-right: 2rem;
  flex-shrink: 0;
}

.career-banner-title {
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 0.5rem;
}

.career-banner-logo-text {
  color: #fff;
}

.career-banner-hiring {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 1.2rem 0 0.5rem 0;
}

.career-banner-desc {
  font-size: 1.08rem;
  color: #eee;
}

.career-banner-label {
  position: absolute;
  right: 3rem;
  top: 2.5rem;
  font-size: 2rem;
  color: #888;
  font-weight: 700;
  opacity: 0.3;
}

.career-main {
  max-width: 900px;
  margin: 0 auto;
  padding: 2.5rem 2rem 2rem 2rem;
}

.career-main-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: #222;
}

.career-main-desc {
  text-align: center;
  color: #555;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
}

.career-divider {
  margin: 2rem 0;
  border: none;
  border-top: 1px solid #bbb;
}

.career-section-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin: 6.2rem 0 0.7rem 0;
  color: #222;
}

.career-section-desc {
  text-align: center;
  color: #555;
  margin-bottom: 1.1rem;
  font-size: 1.08rem;
}

.career-list {
  text-align: center;
  list-style: none;
  padding: 0;
  margin: 0 0 1.5rem 0;
}

.career-list li {
  

  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 0.7rem;
  margin-bottom: 0.7rem;
  color: #444;
  font-size: 1.05rem;
}

