.career-email {
  margin: 2.5rem 0 2rem 0;
  font-size: 1.08rem;
  color: #222;
  text-align: center;
}

.career-openings {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
  margin: 2.5rem 0 2rem 0;
}

.career-opening-card {
  background: #f4f4f4;
  border-radius: 12px;
  padding: 2rem 1.5rem 1.5rem 1.5rem;
  width: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

.career-opening-image {
  width: 70px;
  height: 70px;
  background: #bbb;
  border-radius: 50%;
  margin-bottom: 1.2rem;
}

.career-opening-title {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 0.7rem;
  color: #222;
}

.career-opening-desc {
  color: #555;
  font-size: 1.01rem;
  margin-bottom: 1.2rem;
  text-align: center;
}

.career-opening-apply {
  background: #222;
  color: #fff;
  border: none;
  border-radius: 7px;
  padding: 0.7rem 2.2rem;
  font-size: 1.05rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s;
}

.career-opening-apply:hover {
  background: #444;
}

.career-companies-section {
  margin: 2.5rem 0 2rem 0;
}

.career-companies-title {
  text-align: center;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
  color: #222;
}

.career-companies-list {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.career-company {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60px;
}

.career-company-logo {
  width: 48px;
  height: 48px;
  background: #bbb;
  border-radius: 10px;
  margin-bottom: 0.3rem;
}

.career-company-name {
  font-size: 0.85rem;
  color: #888;
  text-align: center;
}

.career-cta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background: #f4f4f4;
  border-radius: 10px;
  margin: 2.5rem auto 1.5rem auto;
  padding: 1.5rem 2rem;
  width: 90%;
  max-width: 900px;
  font-size: 1.1rem;
  color: #222;
}

.career-cta-btn {
  background: #222;
  color: #fff;
  border: none;
  border-radius: 2rem;
  padding: 0.7rem 2.2rem;
  font-size: 1.05rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s;
}

.career-cta-btn:hover {
  background: #444;
}

.career-footer {
  background: #888;
  color: #fff;
  padding: 2.5rem 2rem 1.5rem 2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 2.5rem;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.footer-logo-icon {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  background: #bbb;
}

.footer-logo-main {
  font-size: 1.3rem;
  font-weight: 700;
  color: #fff;
}

.footer-logo-sub {
  color: #fff;
  opacity: 0.7;
}

.footer-logo-tagline {
  font-size: 0.95rem;
  color: #eee;
  margin-top: 2px;
}

.footer-links {
  display: flex;
  gap: 2.5rem;
}

.footer-title {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #fff;
}

.footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
  font-size: 0.98rem;
}

.footer-links li {
  margin-bottom: 0.3rem;
}

.footer-social {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}

.footer-social-icons {
  display: flex;
  gap: 0.7rem;
}

.footer-social-icon {
  width: 28px;
  height: 28px;
  background: #bbb;
  border-radius: 50%;
  display: inline-block;
}

#animation
.career-banner-logo,
.career-opening-image,
.career-company-logo,
.footer-logo-icon,
.footer-social-icon {
  object-fit: cover;
  display: block;
}

/* Animation: fade-in */
@keyframes fadein {
  from { opacity: 0; transform: translateY(30px);}
  to { opacity: 1; transform: translateY(0);}
}
.animate-fadein {
  animation: fadein 0.9s cubic-bezier(.4,0,.2,1);
}

/* Animation: hover scale */
.animate-hover-scale {
  transition: transform 0.25s cubic-bezier(.4,0,.2,1), box-shadow 0.25s;
}
.animate-hover-scale:hover {
  transform: scale(1.07);
  box-shadow: 0 4px 24px rgba(0,0,0,0.10);
}


