/* Full screen layout */
.dashboard-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #eef2f3, #cfd9df);
  color: #333;
}

/* Header */
.dashboard-header {
  background-color: #ffffff;
  padding: 20px 0;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.dashboard-header h1 {
  font-size: 2.5rem;
  margin: 0;
}

/* Main content */
.dashboard-main {
  flex: 1;
  display: flex;
  margin-top: 50px;
  margin-bottom: 50px;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.dashboard-box {
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 100%;
  max-width: 400px;
}

.dashboard-text {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 30px;
}

/* Buttons */
.dashboard-buttons {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.dashboard-button {
  padding: 12px 20px;
  font-size: 1rem;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.dashboard-button.assign {
  background-color: #007bff;
  color: white;
}

.dashboard-button.assign:hover {
  background-color: #0056b3;
}

.dashboard-button.edit {
  background-color: #28a745;
  color: white;
}

.dashboard-button.edit:hover {
  background-color: #1e7e34;
}

/* Footer */
.dashboard-footer {
  text-align: center;
  padding: 10px 0;
  font-size: 0.9rem;
  color: #777;
  background-color: #f0f0f0;
}
