.photo-edit-form {
  max-width: 100%;
  margin: 0 auto;
  padding: clamp(1rem, 3vw, 2rem);
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: clamp(1rem, 2vw, 1.5rem);
}

.formats-section {
  margin-bottom: 0.5rem;
  width: 100%;
}

.formats-section h3 {
  margin-bottom: 0.75rem;
  color: #555;
  font-size: clamp(1rem, 3vw, 1.1rem);
}

.formats-list {
  display: flex;
  flex-wrap: wrap;
  gap: clamp(0.5rem, 2vw, 0.75rem);
}

.format-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
}

.format-option input[type="radio"] {
  appearance: none;
  width: clamp(0.9rem, 3vw, 1rem);
  height: clamp(0.9rem, 3vw, 1rem);
  border: 2px solid #ccc;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
}

.format-option input[type="radio"]:checked {
  border-color: #e2ce16;
}

.format-option input[type="radio"]:checked::before {
  content: '';
  position: absolute;
  width: 50%;
  height: 50%;
  background-color: #e2ce16;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.format-label {
  font-size: clamp(0.85rem, 2.5vw, 0.9rem);
  white-space: nowrap;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: #444;
  font-size: clamp(0.9rem, 2.5vw, 0.95rem);
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: clamp(0.65rem, 2vw, 0.75rem) clamp(0.9rem, 2vw, 1rem);
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: clamp(0.95rem, 3vw, 1rem);
  width: 100%;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #ffc107;
}

.form-group textarea {
  resize: vertical;
  min-height: clamp(5rem, 20vw, 6rem);
}

.submit-btn {
  background-color: #E6A800;
  color: white;
  padding: clamp(0.75rem, 2vw, 0.9rem);
  border: none;
  border-radius: 20px;
  font-size: clamp(0.95rem, 3vw, 1rem);
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  width: 100%;
  max-width: 200px;
  margin: 0 auto;
  display: block;
}

.submit-btn:hover {
  background-color: #e2ce16;
  transform: translateY(-2px);
}

.submit-btn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
}

select {
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 1em;
}

/* Special adjustments for very small screens */
@media (max-width: 400px) {
  .photo-edit-form {
    padding: 0.8rem;
    gap: 0.8rem;
  }
  
  .formats-list {
    justify-content: space-between;
    gap: 0.3rem;
  }
  
  .submit-btn {
    max-width: 100%;
    padding: 0.8rem;
  }
}

/* Adjustments for landscape mobile */
@media (max-height: 500px) and (orientation: landscape) {
  .photo-edit-form {
    padding: 0.8rem;
    gap: 0.6rem;
  }
  
  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 0.5rem 0.8rem;
  }
  
  .form-group textarea {
    min-height: 4rem;
  }
}