.payment-page {
  background: #fff;
  min-height: 100vh;
}

.payment-top {
  padding: 2.5rem 2rem 1.5rem 2rem;
  max-width: 900px;
  margin: 0 auto;
}

.payment-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.2rem;
  color: #222;
}

.payment-subtitle {
  text-align: center;
  font-size: 1.25rem;
  font-weight: 600;
  margin: 2.2rem 0 1rem 0;
  color: #222;
}

.payment-desc {
  text-align: center;
  color: #555;
  margin-bottom: 1.2rem;
  font-size: 1.08rem;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.5;
}

.payment-method-section {
  display: flex;
  justify-content: center;
  gap: 3rem;
  background: #f4f4f4;
  padding: 2.5rem 0 2.5rem 0;
  margin-bottom: 2rem;
}

.payment-method-left,
.payment-method-right {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.payment-method-label {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
  color: #555;
}

.payment-method-dropdown {
  position: relative;
  width: 220px;
}

.dropdown-btn {
  width: 100%;
  padding: 1rem 1.2rem;
  border-radius: 10px;
  border: none;
  background: #888;
  color: #fff;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: left;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dropdown-arrow {
  font-size: 1.1rem;
  margin-left: 1rem;
}

.dropdown-list {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #fff;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  z-index: 10;
  border: 1px solid #e0e0e0;
}

.payment-method-dropdown:hover .dropdown-list,
.payment-method-dropdown:focus-within .dropdown-list {
  display: block;
}

.dropdown-item {
  padding: 1rem 1.2rem;
  font-size: 1.08rem;
  color: #444;
  cursor: pointer;
  border-bottom: 1px solid #eee;
  background: #fff;
  transition: background 0.2s;
}

.dropdown-item:last-child {
  border-bottom: none;
}

.dropdown-item.selected,
.dropdown-item:hover {
  background: #f4f4f4;
  font-weight: 700;
}

.payment-terms-section {
  max-width: 900px;
  margin: 0 auto 2.5rem auto;
  padding: 0 2rem;
}

.payment-terms-title {
  text-align: center;
  font-size: 1.15rem;
  font-weight: 600;
  margin: 2.2rem 0 1rem 0;
  color: #222;
}

.payment-checkbox-row {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 0.7rem;
  margin: 1.2rem 0 0 0;
  font-size: 0.98rem;
  color: #555;
}


.payment-checkbox-row input{
  width: 1rem;
  height: 1rem;
  cursor: pointer;
  margin-top: 0.2rem;
}

.free-trial-section {
  display: flex;
  background: #e5e3e3;
  margin-top: 2.5rem;
  padding: 3rem 0;
  justify-content: center;
  align-items: flex-start;
  gap: 2.5rem;
}

.free-trial-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  padding-right: 2rem;
}

.free-trial-offer {
  font-size: 1.25rem;
  color: #222;
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.free-trial-big {
  font-size: 3rem;
  font-weight: 800;
  color: #fff;
  line-height: 1.05;
  letter-spacing: 0.02em;
}

.free-trial-form-container {
    background: #fff;
    border-radius: 10px;
    padding: 2rem 2rem 1.5rem 2rem;
    min-width: 340px;
    max-width: 370px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.04);
    margin-top: 3rem;         /* Increased space on top */
    margin-bottom: 2rem;
    margin-right: 3rem;
}

.free-trial-form {
  display: flex;
  flex-direction: column;
  gap: 1.1rem;
}

.free-trial-formats label {
    font-size: 1.05rem;
    font-weight: 600;
    margin-top: 1.2rem;
    margin-bottom: 0.7rem;
    color: #222;
    display: block;
}

.free-trial-formats input[type="radio"] {
  margin-right: 0.3em;
}

.free-trial-form input,
.free-trial-form textarea {
  width: 100%;
  padding: 0.7rem 1rem;
  border-radius: 7px;
  border: 1.5px solid #bbb;
  font-size: 1.08rem;
  background: #f4f4f4;
  font-weight: 500;
  color: #333;
  margin-bottom: 0.2rem;
  resize: none;
}

.free-trial-send-btn {
  background: #888;
  color: #fff;
  border: none;
  border-radius: 7px;
  padding: 0.8rem 0;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  margin-top: 0.5rem;
  transition: background 0.2s;
}

.free-trial-send-btn:hover {
  background: #444;
}

.payment-banner-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8f8f8;
  padding: 2.5rem 4vw;
  margin: 2rem 0 2.5rem 0;
  border-radius: 0;
}

.payment-banner-left {
  flex: 1;
}

.payment-banner-title {
  font-size: 2rem;
  font-style: italic;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.payment-banner-desc {
  font-size: 1.08rem;
  color: #222;
  max-width: 420px;
}

.payment-banner-right {
  flex: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.payment-icons-box {
  display: flex;
  gap: 1.2rem;
  background: #fff;
  border: 1px solid #888;
  border-radius: 14px;
  padding: 1rem 1.5rem;
  min-width: 220px;
  justify-content: center;
  align-items: center;
}

.payment-icon-img {
  width: 56px;
  height: 32px;
  object-fit: contain;
  background: #ccc;
  border-radius: 4px;
  display: block;
}

.payment-icon-placeholder {
  width: 56px;
  height: 32px;
  background: #ccc;
  border-radius: 4px;
  display: block;
}

.payment-banner-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8f8f8;
  padding: 2.5rem 4vw;
  margin: 2rem 0 2.5rem 0;
}

.payment-banner-left {
  flex: 1;
}

.payment-banner-title {
  font-size: 2rem;
  font-style: italic;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.payment-banner-desc {
  font-size: 1.08rem;
  color: #222;
  max-width: 420px;
}

.payment-banner-right {
  flex: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.payment-icons-box {
  display: flex;
  gap: 1.2rem;
  background: #fff;
  border: 1px solid #888;
  border-radius: 14px;
  padding: 1rem 1.5rem;
  min-width: 220px;
  justify-content: center;
  align-items: center;
}

.payment-icon-img-wrap {
  width: 56px;
  height: 32px;
  background: #eee;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.payment-icon-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  background: transparent;
}

