}

.payment-desc {
  text-align: center;
  color: #555;
  margin-bottom: 1.2rem;
  font-size: 1.08rem;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.5;
}


.under {
    margin-top: 10vh;
    font-size: 2.5rem;
    margin-bottom: 1rem;
    text-align: center;
    font-weight: 400;

    /* display: inline-block; */
    padding-bottom: 10px;
}

.under::after {
    content: '';
    display: block;
    width: 10rem;
    height: 3px;
    background-color: #ffa500;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}