.pricing-page {
  background: #fff;
  padding-bottom: 40px;
}

.pricing-main-title {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin-top: 2.5rem;
  margin-bottom: 0.5rem;
  color: #222;
}

.pricing-main-desc {
  text-align: center;
  color: #555;
  margin-bottom: 2.5rem;
  font-size: 1rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.pricing-section-title {
  /* background: #f4f4f4; */
  background-color: #FFBA00;
  color: #222;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.7rem 0 0.7rem 2rem;
  margin-top: 2.5rem;
  border-top: 1px solid #e5e7eb;
  border-bottom: 1px solid #e5e7eb;
  text-align: center;
}

.pricing-section {
  display: flex;
  justify-content: center;
  background: #fff;
  padding: 1.5rem 0;
}

.pricing-section-inner {
  display: flex;
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
  width: 90%;
  max-width: 1100px;
  min-height: 220px;
  overflow: hidden;
}

.pricing-image-placeholder {
  position: relative;
  width: 40%;
  /* background:#FFBA00;; */
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 10px;
  flex-direction: column;
}

.pricing-image-split {
  width: 90%;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.pricing-image-split > div {
  width: 50%;
  height: 100%;
  background: #bdbdbd;
}

.pricing-image-split > div:first-child {
  border-right: 2px solid #fff;
}

.pricing-details {
  flex: 1;
  display: flex;
  flex-direction: row;
  padding: 2rem 2rem 2rem 2.5rem;
  gap: 2.5rem;
  align-items: flex-start;
}

.pricing-meta {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  min-width: 120px;
}

.pricing-meta-label {
  font-size: 0.95rem;
  color: #888;
}

.pricing-meta-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1976d2;
  margin: 0.2rem 0;
}

.pricing-meta-desc {
  font-size: 0.95rem;
  color: #888;
}

.pricing-features-list {
  flex: 1;
}

.pricing-features-title {
  font-size: 1.15rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #1976d2;
}

.pricing-features-list ul {
  list-style: none;
  padding: 0;
  margin: 0 0 1rem 0;
}

.pricing-features-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0;
  font-size: 1.05rem;
  color: #333;
  border-bottom: 1px dotted #e0e0e0;
}

.pricing-feature-price {
  color: #888;
  font-size: 1rem;
  font-weight: 500;
  margin-left: 1.5rem;
}

.pricing-actions {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
}

.pricing-btn,
.pricing-btn-outline {
  border: none;
  outline: none;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 2rem;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border 0.2s;
}

.pricing-btn {
  background: #ffc107;
  color: #222;
  border: 2px solid #ffc107;
}

.pricing-btn:hover {
  background: #ffd740;
  color: #1976d2;
  border-color: #1976d2;
}

.pricing-btn-outline {
  background: #fff;
  color: #1976d2;
  border: 2px solid #1976d2;
}

.pricing-btn-outline:hover {
  background: #e3f2fd;
  color: #1976d2;
}

.pricing-cta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background: #f4f4f4;
  border-radius: 10px;
  margin: 2.5rem auto 1.5rem auto;
  padding: 1.5rem 2rem;
  width: 90%;
  max-width: 900px;
  font-size: 1.1rem;
  color: #222;
}

.pricing-payment-methods {
  display: flex;
  align-items: center;
  gap: 2rem;
  justify-content: center;
  margin: 2rem 0 1.5rem 0;
  font-size: 1.1rem;
  color: #222;
}

.payment-icons {
  display: flex;
  gap: 1rem;
}

.payment-icon {
  width: 48px;
  height: 28px;
  background: #e0e0e0;
  border-radius: 6px;
  display: inline-block;
}

.pricing-footer {
  background: #222;
  color: #fff;
  padding: 2.5rem 2rem 1.5rem 2rem;
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 2.5rem;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.footer-logo img {
  width: 56px;
  height: 56px;
  border-radius: 14px;
  background: #fff;
}

.footer-logo-main {
  font-size: 1.3rem;
  font-weight: 700;
  color: #fff;
}

.footer-logo-sub {
  color: #ffc107;
}

.footer-logo-tagline {
  font-size: 0.95rem;
  color: #bbb;
  margin-top: 2px;
}

.footer-links {
  display: flex;
  gap: 2.5rem;
}

.footer-title {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #ffc107;
}

.footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
  font-size: 0.98rem;
}

.footer-links li {
  margin-bottom: 0.3rem;
}

.footer-social {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}

.footer-social-icons {
  display: flex;
  gap: 0.7rem;
}

.footer-social-icon {
  width: 28px;
  height: 28px;
  background: #444;
  border-radius: 50%;
  display: inline-block;
}

.pricing-image {
  display: block;
  margin: 0 auto;
  border-radius: 10px;
  background: #fff;
  max-width: 100%;
  max-height: 180px;
  object-fit: contain;
}

/* for interactive editing */
.pricing-demo-controls {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.7rem;
  justify-content: center;
}

.pricing-demo-btn {
  background: #fff;
  border: 1px solid #bbb;
  border-radius: 6px;
  padding: 0.3rem 0.8rem;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background 0.2s, border 0.2s;
}

.pricing-demo-btn.active,
.pricing-demo-btn:hover {
  background: #222;
  color: #fff;
  border-color: #222;
}



.our-cc {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5vw;
}

.our-cc h1 {

  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 300;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.our-cc h1::after {
  content: '';
  display: block;
  width: 10rem;
  height: 3px;
  background-color: #ffa500;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}







.service-title {
  font-size: 2rem;
  color: #2c3e50;
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  text-transform: capitalize;
}