.ss2-container {
    font-family: Arial, sans-serif;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    display: flex;
    background-color: #f5f5f5;
  }
  
  .ss2-content {
    flex: 1;
    margin-right: 40px;
  }
  
  .ss2-image-slider {
    position: relative;
    width: 100%;
    height: 400px;
    overflow: hidden;
    cursor: ew-resize;
  }
  
  .ss2-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .ss2-after {
    clip-path: inset(0 50% 0 0);
  }
  
  .ss2-slider {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 4px;
    background: white;
    left: 50%;
    transform: translateX(-50%);
    cursor: ew-resize;
  }
  
  .ss2-slider-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40px;
    height: 40px;
    background: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  }
  
  .ss2-slider-arrow {
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
  }
  
  .ss2-left-arrow {
    border-right: 6px solid #333;
    margin-right: 2px;
  }
  
  .ss2-right-arrow {
    border-left: 6px solid #333;
    margin-left: 2px;
  }
  
  .ss2-label {
    position: absolute;
    top: 20px;
    padding: 5px 10px;
    background: rgba(71, 71, 71, 0.5);
    color: white;
    font-size: 14px;
    transition: opacity 0.3s ease;
  }
  
  .ss2-before-label {
    left: 20px;
  }
  
  .ss2-after-label {
    right: 20px;
  }
  
  .ss2-hidden {
    opacity: 0;
  }
  
  .ss2-text-content {
    flex: 1;
  }
  
  .ss2-text-content h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .ss2-text-content p {
    font-size: 14px;
    line-height: 1.6;
    color: #666;
    margin-bottom: 15px;
  }
  
  .ss2-text-content ul {
    list-style-type: none;
    padding-left: 20px;
    margin-bottom: 20px;
  }
  
  .ss2-text-content li {
    font-size: 14px;
    color: #666;
    margin-bottom: 10px;
    position: relative;
  }
  
  .ss2-text-content li::before {
    content: '•';
    position: absolute;
    left: -20px;
    color: #666;
  }
  
  .ss2-view-pricing {
    display: inline-block;
    color: #000;
    text-decoration: underline;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .ss2-buttons {
    display: flex;
    gap: 10px;
  }
  
  .ss2-button {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #FDB900;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .ss2-button:hover {
    background-color: #e68515;
  }
  
  @media (max-width: 768px) {
    .ss2-container {
      flex-direction: column;
    }
    
    .ss2-content {
      margin-right: 0;
      margin-bottom: 20px;
    }
    
    .ss2-image-slider {
      height: 300px;
    }
  }



  .custom-buttons {
    /* background-color: red; */
    display: flex;
    /* justify-content: center; */
   justify-content: center;
    gap:10px;
    margin-top: 20px;

  }