/* Marquee Payment Methods */
.payment-marquee-container {
  width: 100%;
  background: linear-gradient(135deg, #fff8e1 0%, #ffecb3 100%);
  padding: 2rem 0;
  margin: 3rem 0;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0 20px rgba(255, 204, 0, 0.2);
  border-top: 1px solid #ffd54f;
  border-bottom: 1px solid #ffd54f;
}

.payment-marquee-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.payment-marquee-title {
  color: #ff6f00;
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
  text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
}

.payment-marquee-track {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.payment-marquee {
  display: flex;
  width: fit-content;
  animation: scroll 30s linear infinite;
  will-change: transform;
}

.payment-marquee:hover {
  animation-play-state: paused;
}

.payment-marquee-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
  margin: 0 15px;
  padding: 1rem 1.5rem;
  border-radius: 10px;
  min-width: 120px;
  box-shadow: 0 4px 8px rgba(255, 193, 7, 0.2);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 213, 79, 0.5);
}

.payment-marquee-item:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 8px 16px rgba(255, 152, 0, 0.3);
  background: white;
}

.payment-icon {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  color: #333;
  transition: all 0.3s ease;
}

.payment-marquee-item:hover .payment-icon {
  transform: scale(1.1);
}

.payment-marquee-item span {
  color: #5d4037;
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Payment method specific colors */
.paypal { color: #003087 !important; }
.stripe { color: #635bff !important; }
.apple { color: #000 !important; }
.gpay { color: #4285f4 !important; }
.shopify { color: #5e8e3e !important; }
.bitcoin { color: #f7931a !important; }
.amex { color: #016fd0 !important; }

@keyframes scroll {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .payment-marquee-title {
    font-size: 1.5rem;
  }
  
  .payment-marquee-item {
    min-width: 100px;
    padding: 0.8rem 1rem;
    margin: 0 10px;
  }
  
  .payment-icon {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .payment-marquee {
    animation-duration: 20s;
  }
  
  .payment-marquee-item {
    min-width: 80px;
    padding: 0.6rem 0.8rem;
    margin: 0 8px;
  }
  
  .payment-icon {
    font-size: 1.8rem;
  }
  
  .payment-marquee-item span {
    font-size: 0.7rem;
  }
}