/* OurServices.css */

/* Main container styles */
.our-services {
  padding: 2rem 2rem;
  text-align: center;
  font-family: 'Roboto Slab', serif;
}

.our-services h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  font-weight: 300;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.our-services h2::after {
  content: '';
  display: block;
  width: 10rem;
  height: 3px;
  background-color: #ffa500;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.services-description {
  max-width: 800px;
  margin: 0 auto 2rem;
  color: #666;
}

/* Services container */
.services-container {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Service section styles */
.service-section {
  text-align: center;
  margin-bottom: 2rem;
}


.service-title-container {
  display: flex;
  width: 100%;
  background-color: #FFC107; /* Yellow background */
  justify-content: center;
  padding: 8px 0; /* Vertical padding */
  position: relative;
  min-height: 80px;
  align-items: center; /* Vertical centering */
  /* Removed clip-path property */
}

.service-title {
  color: white;
  font-size: 2rem;
  font-weight: 800;
  text-align: center;
  display: inline-block;
  text-transform: uppercase;
  padding: 0 10px; /* Horizontal padding */
  margin: 0;
  /* Removed position and z-index as they're not needed now */
}

