.service-description {
  max-width: 800px;
  margin: 0 auto 2rem;
  color: #666;
}

/* Sub services grid - Strict 3 columns */
.sub-services-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-top: 1.5rem;
}

.sub-service-item {
  background: #fff;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.sub-service-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.sub-service-image {
  margin-bottom: 1rem;
  position: relative;
  height: 250px;
  overflow: hidden;
  border-radius: 6px;
}

.sub-service-item h4 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #444;
}

.sub-service-item p {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 1.5rem;
}

/* View More Subservices Button */
.view-more-subservices-container {
  margin-top: 2rem;
  text-align: center;
}

.view-more-subservices-btn {
  background-color: #ffa500;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 25px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.view-more-subservices-btn:hover {
  background-color: #e69500;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(255, 165, 0, 0.3);
}

/* Service links styles */
.service-links {
  display: flex;
  gap: 16px;
  align-items: center;
  justify-content: center;
}

.service-links a {
  text-decoration: none;
  color: var(--text-color);
  transition: color var(--transition-speed) ease, transform var(--transition-speed) ease;
}

.service-links a:hover {
  color: var(--primary-color);
  transform: translateY(-2px);
}

/* Quote button styles */
.service-links .quote-link {
  border: 2px solid var(--primary-color);
  border-radius: 25px;
  padding: 8px 20px;
  font-weight: 500;
  transition: all var(--transition-speed) cubic-bezier(0.25, 0.46, 0.45, 0.94);
  background-color: var(--primary-color);
  color: var(--bg-color);
}

.service-links .quote-link:hover {
  background-color: #e6a800;
  border-color: #e6a800;
  color: var(--bg-color);
}

/* View More button styles */
.view-more-link {
  border: 2px solid var(--primary-color);
  border-radius: 25px;
  padding: 8px 20px;
  font-weight: 500;
  transition: all var(--transition-speed) cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.view-more-link:hover {
  background-color: var(--primary-color);
  color: var(--bg-color);
}

/* View More Services button styles */
.view-more-container {
  text-align: center;
  margin-top: 2rem;
}

.view-more-btn {
  background-color: var(--primary-color);
  color: var(--bg-color);
  border: none;
  border-radius: 25px;
  padding: 12px 30px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-speed) cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.view-more-btn:hover {
  background-color: #e6a800;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(255, 185, 0, 0.3);
}

/* Responsive design */
@media (max-width: 1024px) {
  .sub-services-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .sub-services-grid {
    grid-template-columns: 1fr;
  }
  
  .service-title {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .our-services {
    padding: 2rem 1rem;
  }
  
  .service-links {
    flex-direction: column;
    gap: 10px;
  }
  
  .service-links a {
    width: 100%;
    text-align: center;
  }
}