.free-trial-page {
  padding: 2rem;
  display: flex;
  flex-direction: row;
  background-color: #FFBA00;
  justify-content: space-around;
  height: 100%;
  overflow: hidden;
}

.free-trial-header {
  margin-bottom: 3rem;
  text-align: left;
  margin-right: 5%;
  width: 40%;
}

.free-trial-header h2 {
  text-decoration: none;
  letter-spacing: -1px;
  font-weight: 300;
  text-transform: initial;
  font-size: 1.5rem;
  color: #333;
}

.form-container {
  background-color: #FFBA00;
  padding: 3rem;
  width: 50%;
  align-self: center;
}

.text {
  color: white;
}

.text1 {
  font-size: 8vw;
  font-weight: 600;
  letter-spacing: -6px;
  margin-bottom: -5vw;
}

.text2 {
  font-size: 5vw;
  font-weight: 600;
}

/* Responsive styles */
@media (max-width: 768px) {
  .free-trial-page {
    flex-direction: column;
    padding: 1rem;
    align-items: center;
  }

  .free-trial-header {
    width: 100%;
    margin-right: 0;
    text-align: center;
    margin-bottom: 1rem;
  }

  .form-container {
    width: 100%;
    padding: 1rem;
  }

  .text1 {
    font-size: 15vw;
    letter-spacing: -4px;
    margin-bottom: -8vw;
  }

  .text2 {
    font-size: 10vw;
  }

  .free-trial-header h2 {
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }
}

@media (max-width: 480px) {
  .text1 {
    font-size: 18vw;
    letter-spacing: -3px;
  }

  .text2 {
    font-size: 12vw;
  }
}