/* ContactUs.css */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

.cu-container {
  position: relative;
  width: 100%;
  min-height: 700px;  
  padding: 2rem;
  background-color: #fafafa;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cu-form {
  width: 100%;
  max-width: 820px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.cu-contact-form {
  background-color: #1abc9c;
  position: relative;
}

.cu-circle {
  border-radius: 50%;
  background: linear-gradient(135deg, transparent 20%, #149279);
  position: absolute;
}

.cu-circle-one {
  width: 130px;
  height: 130px;
  top: 130px;
  right: -40px;
}

.cu-circle-two {
  width: 80px;
  height: 80px;
  top: 10px;
  right: 30px;
}

.cu-contact-form:before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  background-color: #1abc9c;
  transform: rotate(45deg);
  top: 50px;
  left: -13px;
}

.cu-form form {
  padding: 2.3rem 2.2rem;
  z-index: 10;
  overflow: hidden;
  position: relative;
}

.cu-title {
  color: #fff;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1;
  margin-bottom: 0.7rem;
}

.cu-input-container {
  position: relative;
  margin: 1rem 0;
}

.cu-input {
  width: 100% !important;
  outline: none !important;
  border: 2px solid #fafafa !important;
  background: none !important;
  padding: 0.6rem 1.2rem !important;
  color: #fff !important;
  font-weight: 500 !important;
  font-size: 0.95rem !important;
  letter-spacing: 0.5px !important;
  border-radius: 5px !important;
  transition: 0.3s !important;
}


textarea.cu-input {
  padding: 0.8rem 1.2rem;
  min-height: 150px;
  border-radius: 5px;
  resize: none;
  overflow-y: auto;
}

.cu-input-container label {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  padding: 0 0.4rem;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 400;
  pointer-events: none;
  z-index: 1000;
  transition: 0.5s;
}

.cu-input-container.cu-textarea label {
  top: 1rem;
  transform: translateY(0);
}

.cu-btn {
  padding: 0.6rem 1.3rem;
  background-color: #fff;
  border: 2px solid #fafafa;
  font-size: 0.95rem;
  color: #1abc9c;
  line-height: 1;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  margin: 0;
  width: 100%;
}

.cu-btn:hover {
  background-color: transparent;
  color: #fff;
}

.cu-input-container span {
  position: absolute;
  top: 0;
  left: 25px;
  transform: translateY(-50%);
  font-size: 0.8rem;
  padding: 0 0.4rem;
  color: transparent;
  pointer-events: none;
  z-index: 500;
}

.cu-input-container span:before,
.cu-input-container span:after {
  content: "";
  position: absolute;
  width: 10%;
  opacity: 0;
  transition: 0.3s;
  height: 5px;
  background-color: #1abc9c;
  top: 50%;
  transform: translateY(-50%);
}

.cu-input-container span:before {
  left: 50%;
}

.cu-input-container span:after {
  right: 50%;
}

.cu-input-container.cu-focus label {
  top: 0;
  transform: translateY(-50%);
  left: 25px;
  font-size: 0.8rem;
}

.cu-input-container.cu-focus span:before,
.cu-input-container.cu-focus span:after {
  width: 50%;
  opacity: 1;
}

.cu-contact-info {
  padding: 2.3rem 2.2rem;
  position: relative;
}

.cu-contact-info .cu-title {
  color: #1abc9c;
}

.cu-text {
  color: #333;
  margin: 1.5rem 0 2rem 0;
}

.cu-information {
  display: flex;
  color: #555;
  margin: 0.7rem 0;
  align-items: center;
  font-size: 0.95rem;
}

.cu-information i {
  color: #1ABC9C;
}

.cu-icon {
  width: 28px;
  margin-right: 0.7rem;
}

.cu-social-media {
  padding: 2rem 0 0 0;
}

.cu-social-media p {
  color: #333;
}

.cu-social-icons {
  display: flex;
  margin-top: 0.5rem;
}

.cu-social-icons a {
  width: 35px;
  height: 35px;
  border-radius: 5px;
  background: linear-gradient(45deg, #1abc9c, #149279);
  color: #fff;
  text-align: center;
  line-height: 35px;
  margin-right: 0.5rem;
  transition: 0.3s;
}

.cu-social-icons a:hover {
  transform: scale(1.05);
}

.cu-contact-info:before {
  content: "";
  position: absolute;
  width: 110px;
  height: 100px;
  border: 22px solid #1abc9c;
  border-radius: 50%;
  bottom: -77px;
  right: 50px;
  opacity: 0.3;
}

.cu-big-circle {
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background: linear-gradient(to bottom, #1cd4af, #159b80);
  bottom: 50%;
  right: 50%;
  transform: translate(-40%, 38%);
}

.cu-big-circle:after {
  content: "";
  position: absolute;
  width: 360px;
  height: 360px;
  background-color: #fafafa;
  border-radius: 50%;
  top: calc(50% - 180px);
  left: calc(50% - 180px);
}

.cu-square {
  position: absolute;
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(181%, 11%);
  opacity: 0.2;
}

/* Add these new styles for the captcha */
.cu-captcha-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.cu-captcha-row .cu-input-container {
  flex: 1;
  margin-right: 1rem;
}

.cu-captcha-image {
  background-color: #fff;
  color: #1abc9c;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 0.6rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s;
}

.cu-captcha-image:hover {
  background-color: #f0f0f0;
}

