}

/* checkbox styles */

.cu-agree-terms {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.cu-agree-terms input[type="checkbox"] {
  margin-right: 10px;
  margin-top: 4px;
}

.cu-agree-terms label {
  font-size: 14px;
  line-height: 1.4;
  color: white;
}

@media (max-width: 850px) {
  .cu-form {
    grid-template-columns: 1fr;
  }

  .cu-contact-info:before {
    bottom: initial;
    top: -75px;
    right: 65px;
    transform: scale(0.95);
  }

  .cu-contact-form:before {
    top: -13px;
    left: initial;
    right: 70px;
  }

  .cu-square {
    transform: translate(140%, 43%);
    height: 350px;
  }

  .cu-big-circle {
    bottom: 75%;
    transform: scale(0.9) translate(-40%, 30%);
    right: 50%;
  }

  .cu-text {
    margin: 1rem 0 1.5rem 0;
  }

  .cu-social-media {
    padding: 1.5rem 0 0 0;
  }
}

@media (max-width: 480px) {
  .cu-container {
    padding: 1.5rem;
  }

  .cu-contact-info:before {
    display: none;
  }

  .cu-square,
  .cu-big-circle {
    display: none;
  }

  .cu-form,
  .cu-contact-info {
    padding: 1.7rem 1.6rem;
  }

  .cu-text,
  .cu-information,
  .cu-social-media p {
    font-size: 0.8rem;
  }

  .cu-title {
    font-size: 1.15rem;
  }

  .cu-social-icons a {
    width: 30px;
    height: 30px;
    line-height: 30px;
  }

  .cu-icon {
    width: 23px;
  }

  .cu-input {
    padding: 0.45rem 1.2rem;
  }

  .cu-btn {
    padding: 0.45rem 1.2rem;
  }
}