.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #fff;
}

.job-details {
  margin-bottom: 3rem;
}

.job-details h1 {
  font-size: 2.5rem;
  color: #1a1a1a;
  margin-bottom: 1rem;
}

.job-description {
  font-size: 1.125rem;
  color: #666;
  line-height: 1.6;
  max-width: 800px;
}

.application-section {
  background-color: #f8f8f8;
  padding: 2rem;
  border-radius: 8px;
}

.application-section h2 {
  font-size: 1.8rem;
  color: #1a1a1a;
  margin-bottom: 2rem;
}

.form-section {
  margin-bottom: 2.5rem;
}

.form-section h3 {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #FFBA00;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

@media (min-width: 640px) {
  .form-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .form-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1rem;
  font-weight: 500;
  color: #333;
}

.form-group input,
.form-group select {
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #FFBA00;
  box-shadow: 0 0 0 2px rgba(255, 186, 0, 0.2);
}

.file-input {
  position: relative;
}

.file-input-container {
  position: relative;
  padding: 1rem;
  border: 2px dashed #ddd;
  border-radius: 6px;
  display: flex;
  align-items: center;
  margin-top: 50px;
  justify-content: center;
  background-color: #fff;
  transition: all 0.3s ease;
}

.file-input-container:hover {
  border-color: #FFBA00;
}

.file-input input[type="file"] {
  position: absolute;
  inset: 0;
  opacity: 0;
  cursor: pointer;
}

.file-help-text {
  font-size: 0.875rem;
  color: #666;
  margin-top: 0.25rem;
}

.error-message {
  background-color: #fee2e2;
  color: #dc2626;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 1.5rem;
}

.form-actions {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-end;
}

.submit-button {
  background-color: #FFBA00;
  color: #fff;
  padding: 0.875rem 2rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button:hover {
  background-color: #e5a800;
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}