:root {
  --primary-color: #FFB900;
  --primary-hover: #FFB900;
  --background: #f9fafb;
  --card-bg: #ffffff;
  --text-dark: #111827;
  --text-muted: #6b7280;
  --border: #e5e7eb;
  --shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.fullscreen-modal {
  position: fixed;
  inset: 0;
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  z-index: 1000;
}

.modal-header {
  padding: 1.25rem 2rem;
  background-color: #fff;
  border-bottom: 1px solid var(--border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 10;
}

.modal-header h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-dark);
}

.close-button {
  background-color: var(--primary-color);
  color: white;
  padding: 0.6rem 1.2rem;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  transition: 0.2s ease;
  cursor: pointer;
}

.close-button:hover {
  background-color: var(--primary-hover);
  transform: scale(1.02);
}

.modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem 2rem;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.loading-spinner,
.button-spinner {
  width: 28px;
  height: 28px;
  border: 3px solid rgba(79, 70, 229, 0.2);
  border-top: 3px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

.jobs-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
}

.job-card {
  background-color: var(--card-bg);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 1.25rem;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.job-card:hover {
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.08);
  transform: translateY(-2px);
}

.job-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.job-header h3 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-dark);
}

.edit-button {
  background: none;
  color: var(--primary-color);
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  transition: 0.2s ease;
  padding: 0.25rem 0.5rem;
}

.edit-button:hover {
  background-color: rgba(79, 70, 229, 0.1);
  border-radius: 6px;
}

.job-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-muted);
  margin-bottom: 0.25rem;
}

.form-group input,
.form-group textarea {
  padding: 0.6rem 0.8rem;
  font-size: 0.9rem;
  border: 1px solid var(--border);
  border-radius: 8px;
  background-color: white;
  transition: border-color 0.2s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

textarea {
  min-height: 100px;
  resize: vertical;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.save-button {
  margin-top: 0.5rem;
  padding: 0.7rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s ease;
}

.save-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.save-button:hover:not(:disabled) {
  background-color: var(--primary-hover);
}

.job-details {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}

.detail-item {
  font-size: 0.9rem;
  color: var(--text-dark);
}

.detail-label {
  font-weight: 600;
  color: var(--text-muted);
}

.detail-link {
  color: var(--primary-color);
  word-break: break-word;
  text-decoration: none;
}

.detail-link:hover {
  text-decoration: underline;
}

/* Scrollbar */
.modal-content::-webkit-scrollbar {
  width: 8px;
}

.modal-content::-webkit-scrollbar-thumb {
  background-color: var(--border);
  border-radius: 4px;
}

.modal-content::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-muted);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  .jobs-grid {
    grid-template-columns: 1fr;
  }

  .form-row {
    grid-template-columns: 1fr;
  }
}