.assign-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.assign-modal-container {
  background-color: #fff;
  width: 80%;
  height: 80%;
  border-radius: 8px;
  padding: 16px 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  position: relative;
  box-sizing: border-box;
}

.assign-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.close-btn {
  background: none;
  border: none;
  font-size: 22px;
  cursor: pointer;
}

.loader {
  text-align: center;
  font-size: 18px;
  margin-top: 40px;
  color: #555;
}

.job-card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  box-sizing: border-box;
}

.job-card {
  background-color: #f8f9fa;
  padding: 12px 14px;
  border-radius: 6px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-width: 0; /* prevents overflow */
  box-sizing: border-box;
}

.job-card h3 {
  font-size: 15px;
  margin-bottom: 6px;
  color: #333;
}

.job-card p {
  font-size: 13px;
  color: #555;
  overflow: hidden;
  margin: 4px 0;
  line-height: 1.3;
}

.editor-select {
  margin-top: 6px;
  padding: 5px;
  border-radius: 4px;
  font-size: 13px;
  border: 1px solid #ccc;
}

.assign-btn {
  margin-top: 10px;
  padding: 6px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 13px;
  cursor: pointer;
}

.assign-btn:disabled {
  background-color: #aaa;
  cursor: not-allowed;
}
